<template>
  <div v-show="loader" id="preloader"></div>
  <form v-if="!this.kodeAkses" class="form-signin">
    <img
      class="mb-4"
      src="/img/images/undraw_transfer_money_rywa.svg"
      alt=""
      width="300"
      height="300"
    />
    <div class="form-group">
      <input
        type="text"
        v-model="form.nama"
        id="text"
        class="form-control form-control-lg rounded-10"
        placeholder="Masukan Nama"
        required
        autofocus
        autocomplete="off"
      />
    </div>

    <div class="form-group">
      <input
        type="text"
        v-model="form.noHP"
        id="text"
        class="form-control form-control-lg rounded-10"
        placeholder="Masukan No HP"
        required
        autofocus
        autocomplete="off"
      />
    </div>

    <button
      @click="buatKode()"
      type="button"
      class="btn btn-light btn-block btn-lg rounded-10"
    >
      Buat Kode
    </button>
    <div class="row mt-3">
      <div class="col-12">
        <span class="text-white">Sudah punya kode Akses? </span
        ><router-link class="badge badge-light" to="/login"
          ><span class="text-primary">Masuk</span></router-link
        >
      </div>
    </div>
  </form>
  <form v-else class="form-signin">
    <img
      class="mb-4"
      src="/img/images/undraw_transfer_money_rywa.svg"
      alt=""
      width="300"
      height="300"
    />

    <div class="alert-success rounded-10 shadow p-3 mb-3">
      <h5>
        <span
          ><svg
            xmlns="http://www.w3.org/2000/svg"
            width="25"
            height="25"
            fill="currentColor"
            class="bi bi-info-circle"
            viewBox="0 0 16 16"
          >
            <path
              d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"
            />
            <path
              d="m8.93 6.588-2.29.287-.082.38.45.083c.294.07.352.176.288.469l-.738 3.468c-.194.897.105 1.319.808 1.319.545 0 1.178-.252 1.465-.598l.088-.416c-.2.176-.492.246-.686.246-.275 0-.375-.193-.304-.533L8.93 6.588zM9 4.5a1 1 0 1 1-2 0 1 1 0 0 1 2 0z"
            /></svg
        ></span>
        Halo {{ this.form.nama }}
      </h5>
      <p>
        <strong><br />Kode Akses Anda : {{ this.kodeAkses }}</strong>
        <br /><br />Silahkan screenshot atau salin untuk menyimpan kode akses
        ini.
      </p>
    </div>

    <button
      @click="login()"
      type="button"
      class="btn btn-light btn-block btn-lg rounded-10"
    >
      Masuk
    </button>
  </form>
  <p class="mt-5 mb-3 text-light">
    adxpay&copy; 2021-{{ new Date().getFullYear() }}
  </p>
</template>

<script>
import axios from "axios";
import Swal from "sweetalert2";

export default {
  name: "CusDaftarView",
  props: ["ipAdress", "transaksi", "dataHutang"],

  data() {
    return {
      loader: false,
      form: {
        nama: "",
        noHP: "",
      },
      kodeAkses: null,
    };
  },
  methods: {
    buatKode() {
      if (this.form.nama == "") {
        Swal.fire({
          icon: "error",
          title: "Nama Kosong",
          text: "Silahkan masukan nama Anda!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else if (this.form.noHP == "") {
        Swal.fire({
          icon: "error",
          title: "No HP Kosong",
          text: "Silahkan masukkan no HP Anda!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else {
        this.loader = true;
        axios
          .post(
            this.ipAdress +
              "api/daftar-cus?nama=" +
              this.form.nama +
              "&no_hp=" +
              this.form.noHP
          )
          .then((response) => {
            this.kodeAkses = response.data.kode;
            Swal.fire({
              icon: response.data.status,
              title: "Berhasil",
              text: response.data.message,
              showConfirmButton: false,
              timer: 1500,
            });
          })
          .catch((error) => console.log(error))
          .finally(() => (this.loader = false));
      }
    },
    login() {
      this.loader = true;
      axios
        .post(this.ipAdress + "api/login?username=" + this.kodeAkses)
        .then((response) => {
          if (response.data.message == "Unauthorized") {
            Swal.fire({
              icon: "error",
              title: "Gagal",
              text: "Kode Akses Salah",
              showConfirmButton: false,
              timer: 1500,
            });
          } else {
            window.location.replace("/customer/dashboard");
            Swal.fire({
              icon: "success",
              title: "Login Berhasil",
              text: "Selamat Datang " + response.data.data.name,
              showConfirmButton: false,
              timer: 1500,
            });
            localStorage.setItem(
              "SessionUser",
              JSON.stringify(response.data.data)
            );
          }
        })
        .catch((error) => console.log(error))
        .finally(() => (this.loader = false));
    },
  },
};
</script>

<style>
html,
body {
  text-align: center;
  height: 100%;
}

body {
  display: -ms-flexbox;
  display: -webkit-box;
  display: flex;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  padding-top: 40px;
  padding-bottom: 40px;
  background-color: #1583dd;
}

.form-signin {
  width: 100%;
  max-width: 330px;
  padding: 15px;
  margin: 0 auto;
}
.form-signin .checkbox {
  font-weight: 400;
}
.form-signin .form-control {
  position: relative;
  box-sizing: border-box;
  height: auto;
  padding: 10px;
  font-size: 16px;
}
.form-signin .form-control:focus {
  z-index: 2;
}

.rounded-5 {
  border-radius: 10px;
}

.rounded-10 {
  border-radius: 20px;
}

#preloader {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 999;
  width: 100%;
  height: 100%;
  overflow: visible;
  background: #ffffff url("@/assets/loader/96x96 (2).gif") no-repeat center
    center;
}
</style>